#app3{
    width: 50vw;
    height: 50vh;
    border: 1px solid red;
    border-top:0 ;
}
#app3 .square{
    width: 50px;
    height: 50px;
    border: 1px solid #000;
    position: relative;
    top: 100px;
    left: 50px;
    transition:all .5s linear;
}
#app3 .active{
    left: 100px;
}